@import '~antd/dist/antd.css';

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header {
  background: linear-gradient(to right, #12C65E, #34B6E3);
}
.content {
  background: linear-gradient(to right, rgba(255, 255, 255, 0.94), #9cdde3);
}
.headerText {
  color: #ffffff;
  font:30px Georgia, "Times New Roman", Times, serif; text-decoration:none;
}
.shadow{
  text-shadow: 3px 5px 5px #656B79;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }


}
